@use '../utils/variables' as *;
@use '../utils/mixin';

.btn {
  border: 1px solid $border;
  background: transparent;
  color: $text;
  padding: $button-padding;
  border-radius: 10px;
  text-decoration: none;
  cursor: none;

  @include mixin.minTablet {
    cursor: pointer;
  }

  &.primary {
    background: $brand;
    border-color: $brand;
    color: #fff;
  }

  &.ghost {
    background: transparent;
  }

  &.small {
    padding: 8px 12px;
    font-size: 16px;
    @include mixin.minTablet {
      transition: border 250ms ease;
    }
  }

  @include mixin.minTablet {
    &.small:hover {
      border: 1 solid $accent;
    }
  }

  &.wide {
    width: 100%;
  }

  &.like {
    position: absolute;
    top: 24px;
    right: 24px;
    // fill: none;
    // stroke: $brand;
    // stroke-width: 2px;
    padding: 0;
    outline: none;
    background-color: transparent;
    border: none;
    transition: all 300ms ease;
  }

  // &.like:focus {
  //   fill: $accent;
  //   stroke: none;
  // }
}

.btn-box-recommended {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.icon-res {
  @include mixin.tablet {
    width: 32px;
    height: 32px;
  }

  @include mixin.desktop {
    width: 48px;
    height: 48px;
  }
}

.icon-like {
  display: inline-flex;
  transition: transform 300ms ease;

  svg path {
    fill: none;
    stroke: $brand;
    stroke-width: 2px;
    transition: all 300ms ease;
  }

  &.active svg path {
    fill: $accent;
    stroke: $accent;
  }

  @include mixin.desktop {
    cursor: pointer;
    &:hover svg path {
      stroke: $accent;
    }
  }
}

.checkout-btn {
  @include mixin.short-font(18px, 700, 0.01em);
  width: 100%;
  padding: 15px;
  font-size: 18px;
  font-weight: bold;
  background: $bg;
}
