.burger-menu {
  z-index: 995;
  visibility: hidden;
  opacity: 0;
  width: 100vw;
  height: 100vh;
  transition: opacity .25s ease-in-out, visibility .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
}

.burger-menu[aria-hidden="false"] {
  visibility: visible;
  opacity: 1;
}

.burger-menu-backdrop {
  cursor: none;
  background: #00000080;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (width >= 744px) {
  .burger-menu-backdrop {
    cursor: pointer;
  }
}

.burger-menu-content {
  background: #fff;
  width: 100vw;
  height: 100vh;
  transition: transform .25s ease-in-out;
  position: absolute;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(100%);
  box-shadow: -5px 0 20px #0000001a;
}

.burger-menu[aria-hidden="false"] .burger-menu-content {
  transform: translateX(0);
}

.burger-menu-close {
  cursor: none;
  background-color: #0000;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 8px;
